.wrap {
  display: flex;
  align-items: center;
}

.date {
  font-size: 14px;
  font-weight: 600;
  margin-right: 20px;
}

.navigation_week {
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  color: #f1f1f1;
}

.navigation_week:hover {
  color: #fff;
}

.navigation_week_disabled {
  background: #f1f1f1;
}

.line {
  border: 1px solid #fff;
  height: 15px;
  margin-left: 8px;
  margin-right: 8px;
}
